import { products as allProducts } from '@/lib/all-products-data';
import { Brand, Category, Product } from '@/lib/schema';
import * as brandRepo from '@/repositories/static/brandRepository';
import * as categoryRepo from '@/repositories/static/categoryRepository';


interface BrandStats {
  [key: string]: { productCount: number; categoryCount: number };
}

interface CategoryStats {
  [key: string]: { productCount: number; brandCount: number };
}

const products: Product[] = allProducts.map(p => ({ ...p, imageUrl: p.images[0] }));

export const all = async (): Promise<Product[]> => {
  return products;
};

export const findBySku = async (sku: string): Promise<Product | null> => {
  const product = products.find((p) => p.sku === sku);
  return product ? { ...product, imageUrl: product.images[0] } : null;
};

export const findBySlug = async (slug: string): Promise<Product | null> => {
  const product = products.find((p) => p.slug === slug);
  return product ? { ...product, imageUrl: product.images[0] } : null;
};

export const findByBrand = async (brandSlug: string): Promise<Product[]> => {
  const brand = await brandRepo.findBySlug(brandSlug);
  if (!brand) {
    return [];
  }
  const brandProducts = products.filter((p) => p.brand === brand.name);
  return brandProducts.map(p => ({ ...p, imageUrl: p.images[0] }));
};

export const findByCategory = async (categorySlug: string): Promise<Product[]> => {
  const category = await categoryRepo.findBySlug(categorySlug);
  if (!category) {
    return [];
  }
  const categoryProducts = products.filter((p) => p.categorySlug === category.slug);
  return categoryProducts.map(p => ({ ...p, imageUrl: p.images[0] }));
};

export const search = async (query: string): Promise<Product[]> => {
  const lowerCaseQuery = query.toLowerCase();
  const searchResults = products.filter(
    (p) =>
      p.name.toLowerCase().includes(lowerCaseQuery) ||
      p.description.toLowerCase().includes(lowerCaseQuery)
  );
  return searchResults.map(p => ({ ...p, imageUrl: p.images[0] }));
};

export const getBrandStats = async (): Promise<BrandStats> => {
  return products.reduce((acc: BrandStats, product) => {
    if (!acc[product.brand]) {
      acc[product.brand] = { productCount: 0, categoryCount: 0 };
    }
    acc[product.brand].productCount++;
    return acc;
  }, {});
};

export const getCategoryStats = async (): Promise<CategoryStats> => {
  return products.reduce((acc: CategoryStats, product) => {
    const categoryName = product.category;
    if (!acc[categoryName]) {
      acc[categoryName] = { productCount: 0, brandCount: 0 };
    }
    acc[categoryName].productCount++;
    return acc;
  }, {});
};
